	import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
	import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'
    export default {
        name: "my_yuyue",
				components: {
				    uniSwipeAction,
						uniSwipeActionItem
				},
        data() {
            return {
                appointment_ticket: [],
                appointment_guide: [],
                appointment_hotel: [],
								ticketOptions:[{
								            text: '取消预约',
								            style: {
								                backgroundColor: '#e54d42'
								            }
								        }],
								guideOptions: [{
									text: '取消预约',
									style: {
									    backgroundColor: '#e54d42'
									}
								}],
								hotelOptions: [{
									text: '取消预约',
									style: {
									    backgroundColor: '#e54d42'
									}
								}]
            }
        },
        onShow() {
            this.getAllMyYuyue()
        },
        methods: {
						onClickOfHotel(item) {
							this.removeAppointment(item.id)
						},
						onClickOfGuide(item) {
							this.removeAppointment(item.id)
						},
						onClickOfTicket(item){
							this.removeAppointment(item.id)
					  },
						removeAppointment(id) {
							var _this = this
							uni.request({
								url: _this.$BASE_HOST + 'appointment/delete',
								method:'GET',
								data:{
									'id': id
								},
								success(res) {
									console.log(res)
									_this.getAllMyYuyue()
								},
								fail(err) {
									console.log(err)
								}
							})
						},
					  change(open){
					      // console.log('当前开启状态：'+ open)
					  },
            getAllMyYuyue() {
                var _this = this
                uni.request({
                    url: _this.$BASE_HOST + 'appointment/getAllMyAppointment',
                    method: 'get',
                    data: {
                        'user_id': uni.getStorageSync('local_userinfo').id
                    },
                    success: function (res) {
                        _this.appointment_ticket = res.data.data.ticket
                        _this.appointment_guide = res.data.data.guide
                        _this.appointment_hotel = res.data.data.hotel
                    },
                    fail: function (err) {
                        console.log(err)
                    }
                })
            }
        }
    }
